import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
	preloaderShow: false,
	results: '',
	count: '',
    items: [
			{
				url: '/nasaimages/galaxies',
				text: ''
			},
			{
				url: '/nasaimages/sun',
				text: ''
			},
			{
				url: '/nasaimages/mercury',
				text: ''
			},
			{
				url: '/nasaimages/venus',
				text: ''
			},
			{
				url: '/nasaimages/earth',
				text: ''
			},
			{
				url: '/nasaimages/mars',
				text: ''
			},
			{
				url: '/nasaimages/jupiter',
				text: ''
			},
			{
				url: '/nasaimages/saturn',
				text: ''
			},
			{
				url: '/nasaimages/uranus',
				text: ''
			},
			{
				url: '/nasaimages/neptune',
				text: ''
			},
			{
				url: '/nasaimages/pluto',
				text: ''
			}
		]
  },
  getters: {
	preloaderShow(state){
		return state.preloaderShow;
	},
	count(state){
		return state.count;
	},
    items(state){
			return state.items;
		},
	results(state){
			return state.results;
		}
  },
  mutations: {
	preloaderShow(state) {
		state.preloaderShow = false;
		setTimeout(() => {
			state.preloaderShow = true;
		}, 1000);
	},
	originalOpen(state, data) {
		state.count = data.index;
		state.results = data.results;
	},
	prev(state) {
		if (state.count > 0) {
			state.count--;
		}	
	},
	next(state) {
		state.count++;
	}	
	
  },
  actions: {

  }
})
